import {AppBar, Avatar, Button, Grid, IconButton, TextField, Toolbar, Typography, Link} from '@material-ui/core';
import React, {CSSProperties} from 'react';
import TeamCreateViewModel from '../viewModels/TeamCreateViewModel';
import App from '../../App';
import Layout from "../../../application/Layout";
import MobileDetect from "../../../application/MobileDetect";
import Title from "../../../application/components/Title";
import avatar1 from "../../../assets/img/1.jpg";
import avatar2 from "../../../assets/img/2.jpg";
import avatar3 from "../../../assets/img/3.jpg";
import Themes from "../../../application/Themes";
import TeamEditViewModel from "../viewModels/TeamEditViewModel";
import MenuIcon from "@material-ui/icons/Menu";
import avatar from "../../../assets/img/1.jpg";
import GroupAddTwoToneIcon from "@material-ui/icons/GroupAddTwoTone";
import MiniProfile from "../../dashboard/views/MiniProfile";
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';
import HighlightOffTwoToneIcon from '@material-ui/icons/HighlightOffTwoTone';
import ShareTwoToneIcon from '@material-ui/icons/ShareTwoTone';
import MailTwoToneIcon from '@material-ui/icons/MailTwoTone';

export default class TeamDetail extends TeamEditViewModel {

    css = {
        form: {
            width: MobileDetect.Any() ? '100%' : '60%',
            marginTop: Layout.box(4).top,
            display: 'grid',
        } as CSSProperties,
        selected: {
            border: `3px solid ${Themes.theme.palette.success.main}`
        } as CSSProperties,
        unselected: {
            border: `3px solid ${Themes.theme.palette.action.disabled}`
        } as CSSProperties
    }

    notify() {
        return <App>
            <Grid container alignContent='flex-start' alignItems='flex-start' justify='center'>

                <form style={this.css.form}>
                    <Title title={this.payload?.teamName}
                           subtitle={'equipe criade em 01/06/2020'}
                    >
                        <IconButton size={'medium'}>
                            <HighlightOffTwoToneIcon color={'error'}/>
                        </IconButton>
                        <IconButton size={'medium'}>
                            <EditTwoToneIcon color={'primary'}/>
                        </IconButton>
                    </Title>
                    <MiniProfile/>

                    <Title title={'MEMBROS'}>
                        {
                            MobileDetect.Any()
                                ? < IconButton color={'primary'} size={'medium'}>
                                    <Link href={'https://api.whatsapp.com/send?text="http://google.com"'} variant={'button'}
                                          component={'a'}>
                                        <ShareTwoToneIcon/>
                                    </Link>
                                </IconButton>
                                : <IconButton color={'primary'} size={'medium'}>
                                        <MailTwoToneIcon/>
                                </IconButton>
                        }
                    </Title>

                    <Grid container spacing={3}>
                        <Grid item xs={3} sm={2}>
                            <Avatar style={this.css.unselected} src={avatar1}>OP</Avatar>
                        </Grid>
                        <Grid item xs={3} sm={2}>
                            <Avatar style={this.css.unselected} src={avatar2}>OP</Avatar>
                        </Grid>
                        <Grid item xs={3} sm={2}>
                            <Avatar style={this.css.unselected} src={avatar3}>OP</Avatar>
                        </Grid>
                        <Grid item xs={3} sm={2}>
                            <Avatar style={this.css.unselected} src={avatar1}>OP</Avatar>
                        </Grid>
                        <Grid item xs={3} sm={2}>
                            <Avatar style={this.css.unselected} src={avatar1}>OP</Avatar>
                        </Grid>
                    </Grid>
                </form>

            </Grid>
        </App>
    }
}