import Themes from '../../../application/Themes';

﻿import DashboardViewModel from '../viewModels/DashboardViewModel';
import React, {CSSProperties} from 'react';
import {
    AppBar,
    Container,
    IconButton,
    Typography,
    Button,
    Toolbar,
    Grid,
    Paper,
    Avatar,
    Divider
} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu'
import avatar from '../../../assets/img/1.jpg'

export default class MiniProfile extends DashboardViewModel {

    css = {
        avatar: {
            width: 120,
            height: 120,
            border: `4px solid ${Themes.theme.palette.primary.main}`
        } as CSSProperties
    };

    notify(): React.ReactNode {
        return (
                <Grid justify={'center'} direction={'column'} alignItems={'center'} container>
                    <Avatar style={this.css.avatar} src={avatar}>OP</Avatar>
                    <Typography variant={'button'}>
                        <strong>
                            Dr. Wagner de Souza Filho
                        </strong>
                    </Typography>
                    <Typography variant={'caption'}>
                        Medicina - 1998 - UFRN
                    </Typography>
                    <Typography variant={'caption'}>
                        CRM 1234
                    </Typography>
                </Grid>
        );
    }
}

